.section.alternating-content .item {
    width: 100%;
    padding: 70px 30px;
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
}
@media (min-width: $(desktop)) {
    .section.alternating-content .item {
        padding: 70px 60px;
    }
}
.section.alternating-content .item .title h2 {
    font-size: 29px;
    font-weight: bold;
    line-height: 1.17;
}
.section.alternating-content .item .body,
        .section.alternating-content .item .body p {
    font-size: 16px;
    font-weight: normal;
    line-height: 1.75;
}
.section.alternating-content .item .text {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
    width: calc(99.99% * 1/1 - (30px - 30px * 1/1));
}
.section.alternating-content .item .text:nth-child(1n) {
    margin-right: 30px;
    margin-left: 0;
}
.section.alternating-content .item .text:last-child {
    margin-right: 0;
}
.section.alternating-content .item .text:nth-child(1 '' ''n) {
    margin-right: 0;
    margin-left: auto;
}
@media (min-width: $(desktop)) {
    .section.alternating-content .item .text {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 2/5 - (30px - 30px * 2/5));
    }
    .section.alternating-content .item .text:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.alternating-content .item .text:last-child {
        margin-right: 0;
    }
    .section.alternating-content .item .text:nth-child(5 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.section.alternating-content .item .image {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
    width: calc(99.99% * 1/1 - (30px - 30px * 1/1));
}
.section.alternating-content .item .image:nth-child(1n) {
    margin-right: 30px;
    margin-left: 0;
}
.section.alternating-content .item .image:last-child {
    margin-right: 0;
}
.section.alternating-content .item .image:nth-child(1 '' ''n) {
    margin-right: 0;
    margin-left: auto;
}
@media (min-width: $(desktop)) {
    .section.alternating-content .item .image {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 3/5 - (30px - 30px * 3/5));
    }
    .section.alternating-content .item .image:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.alternating-content .item .image:last-child {
        margin-right: 0;
    }
    .section.alternating-content .item .image:nth-child(5 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.section.alternating-content .item .image img {
    max-width: 100%;
}
.section.alternating-content .item:nth-child(odd) {
    background: $white;
}
.section.alternating-content .item:nth-child(odd)::after {
    width: 0;
    height: 0;
    border-style: solid;
    margin-left: -26 px;
    border-width: 26 px 26 px 0 26 px;
    border-color: $white transparent transparent transparent;
    position: absolute;
    top: 100%;
    left: 50%;
    display: block;
    content: ' ';
    z-index: 2;
}
.section.alternating-content .item:nth-child(odd) .text {
    -webkit-box-ordinal-group: 2;
    -webkit-order: 1;
        -ms-flex-order: 1;
            order: 1;
}
.section.alternating-content .item:nth-child(odd) .text .title h2,
                .section.alternating-content .item:nth-child(odd) .text .body,
                .section.alternating-content .item:nth-child(odd) .text .body p {
    color: $black;
}
@media (min-width: $(desktop)) {
    .section.alternating-content .item:nth-child(odd) .image {
        -webkit-box-ordinal-group: 3;
        -webkit-order: 2;
            -ms-flex-order: 2;
                order: 2;
    }
}
.section.alternating-content .item:nth-child(odd) .button {
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: $blue_light;
}
.section.alternating-content .item:nth-child(odd) .button:hover {
    box-shadow: 0 0 0 4px $blue_light,
            inset 0 0 0 3px $white;
    color: #fff;
}
.section.alternating-content .item:nth-child(even) {
    background: $blue_light;
}
.section.alternating-content .item:nth-child(even)::after {
    width: 0;
    height: 0;
    border-style: solid;
    margin-left: -26 px;
    border-width: 26 px 26 px 0 26 px;
    border-color: $blue_light transparent transparent transparent;
    position: absolute;
    top: 100%;
    left: 50%;
    display: block;
    content: ' ';
    z-index: 2;
}
.section.alternating-content .item:nth-child(even) .text {
    -webkit-box-ordinal-group: 3;
    -webkit-order: 2;
        -ms-flex-order: 2;
            order: 2;
    margin-right: 0;
}
.section.alternating-content .item:nth-child(even) .text .title h2,
                .section.alternating-content .item:nth-child(even) .text .body,
                .section.alternating-content .item:nth-child(even) .text .body p {
    color: $white;
}
.section.alternating-content .item:nth-child(even) .image {
    margin-right: 30px;
    -webkit-box-ordinal-group: 2;
    -webkit-order: 1;
        -ms-flex-order: 1;
            order: 1;
}
.section.alternating-content .item:nth-child(even) .button {
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: $white;
    color: $blue_light;
}
.section.alternating-content .item:nth-child(even) .button:hover {
    box-shadow: 0 0 0 4px $white,
            inset 0 0 0 3px $blue_light;
    color: $blue_light;
}
.section.alternating-content .item:last-child::after {
    display: none;
}
